@import "../App.scss";

#app .splash-page {

  nav {
    position:absolute;
    width:100%;
    max-width:1400px;
    margin:0 auto;  
    text-align:right;
    box-sizing:border-box;
    padding:15px 25px;
    z-index:2;
    
    ul, li {
      padding:0;
      margin:0;
      list-style-type:none;
    }

    li {
      display:inline;
      padding:0 0 0 30px;

      a {
        color:rgba(255,255,255,0.7);
        text-decoration:none;
      }
      
      a:hover {
        text-decoration:underline;
      }

    }

  }

  section {

    h1, h2, h3 {
      padding:0;
      margin:0;
    }

  }

  section {
    position:relative;
    -webkit-transform:skew(0, -2.76deg) translateY(calc(50vw/-18));
        -ms-transform:skew(0, -2.76deg) translateY(calc(50vw/-18));
            transform:skew(0, -2.76deg) translateY(calc(50vw/-18));
  }

  section, footer {

    a {
      text-decoration:none;
      color:$signature_color_dark;
    }

    a:hover {
      text-decoration:underline;
    }

    h4 {
      font-family: 'Slabo 27px', serif;
      font-weight:normal;
      font-size:28px;
      padding:10px 0;
      color:rgba($signature_color, 0.8);
    }

    h5 {
      font-weight:normal;
      font-size:20px;
      padding-bottom:40px;
    }

    p {
      padding:10px 0 20px 0;
      font-size:18px;
      max-width:1200px;
      box-sizing:border-box;
      line-height:160%;
    }

  }

  section > * {
    -webkit-transform: skew(0, 2.76deg) translateY(calc(50vw/18));
        -ms-transform: skew(0, 2.76deg) translateY(calc(50vw/18));
            transform: skew(0, 2.76deg) translateY(calc(50vw/18));
    margin-top: calc(50vw/-12);
  }

  section.darkened {
    background-color:rgba($signature_color, 0.75);

    * {
      color:#fff;
    }

    a {
      color:rgba(#fff, 0.6);
      font-weight:bold;
    }
  }
      
  section.splash-header {
    background-color:$signature_color;
    height:160px;
    box-shadow:0 0 6px rgba(0,0,0,0.5);
    z-index:1;

    img {
      position:absolute;
      top:15px;
      left:15px;
      width:280px;
    }

    @media only screen and (max-width: 640px) {

      img {
        left:0;
        right:0;
        margin:0 auto;
      }

    }

  }

  section.deco-image {
    height:430px;
    background-size:cover;
    background-repeat:no-repeat;
    background-position:center 20%;

    .title {
      position:absolute;
      bottom:75px;
      right:50px;
      text-shadow:0 0 9px #000;
      text-align:right;
      font-family: 'Slabo 27px', serif;

      h3 {
        color:#fff;
        font-size:40px;
      }

      h4 {
        color:#fff;
        font-size:34px;
        line-height:150%;
        padding:0;
      }
        
    }

  }

  @media only screen and (max-width: 640px) {

    section.deco-image {
      height:340px;

      .title {

        h3 {
          font-size:34px;
        }

        h4 {
          font-size:28px;
        }

      }

    }

  }

  section.deco-image::before {
    background-color:$signature_color;
    opacity:0.15;
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    content:'';
  }

  section.intro,
  section.team {
    padding:120px 80px;

    p.to-database {
      padding-top:30px;
    }

  }

  section.the-map {
    height:600px;
    overflow:hidden;

    .leaflet-container {
      position:relative;
      top:-20px;
      height:700px;

      img {
        image-rendering:pixelated;
      }

      .leaflet-popup-close-button {
        display:none;
      }

      .leaflet-tetrastylon-close-button {
        position:absolute;
        top:6px;
        right:6px;
        font-size:20px;
        cursor:pointer;
        color:rgba(0,0,0,0.2);
      }

      .leaflet-tetrastylon-close-button:hover {
        color:rgba(0,0,0,0.5);
      }

      .leaflet-popup-content-wrapper {
        border-radius:0;
        padding:0;

        .leaflet-popup-content {
          width:400px !important;
          margin:0;
          padding:0;
          display:flex;
          flex-direction:row;
          line-height:200%;

          .popup-thumbnail img {
            width:120px;
            height:120px;
            background-color:rgba($signature_color,.15);
          }

          .popup-body {
            position:relative;
            vertical-align:top;
            padding:10px 30px 10px 10px;

            h1 {
              font-size:17px;
            }

            h2 {
              font-size:15px;
              color:#5d5a5a;
            }

            a {
              position:absolute;
              bottom:10px;
              font-size:15px;
              text-decoration:none;
            }

            a:hover {
              text-decoration:underline;
            }
          
          }

        }

      }

    }

    .layer-toggle {
      position:absolute;
      bottom:20px;
      right:30px;

      -webkit-transform:rotate(-0.6deg);
          -ms-transform:rotate(-0.6deg);
              transform:rotate(-0.6deg);

      button {
        background-color:rgba(0,0,0,0.65);
      }
    }

  }

  section.team {

    p:first-child,
    img:first-child {
      margin-right:50px;
    }

    .team-member {
      display:flex;
      flex-direction:row;
      padding-bottom:40px;
    }

    p {
      padding-top:0;
    }

    img {
      margin-top:10px;
      width:260px;
      height:260px;
      align-self:flex-start;
      border-radius:100%;
      border:5px solid #fff;
    }

  }

  section.acknowledgements {
    padding:120px 80px 100px 80px;

    .funder-logos {
      padding-top:40px;

      img.lasapienza {
        margin-right:70px;
        width:220px;
      }

      img.ec {
        width:250px;
        padding-bottom:18px;
      }

    }

  }

  footer {
    position:relative;
    background-color:$signature_color_darker;
    padding:30px;

    .footer-content {
      color:rgba(255,255,255,0.8);  
      line-height:160%;
      font-size:17px;

      div {
        padding-top:20px;
      }

      span {
        white-space:nowrap;
        display:block;
      }

      a {
        color:rgba(255, 255, 255, .9);
        text-decoration:underline;
      }
    }
  }

}