@import "../App.scss";

.project {

  main {
    padding:60px 80px;
  
    section {
      position:relative;
      padding:0;
      margin:0;

      h2 {
        font-family: 'Slabo 27px', serif;
        font-weight:normal;
        font-size:30px;
        padding:10px 0;
        color:rgba($signature_color, 0.8);
      }

      p {
        padding:10px 0;
        font-size:18px;
        max-width:1200px;
        box-sizing:border-box;
        line-height:160%;
      }

      .house-types {
        margin:70px 0;
        text-align:center;

        img {
          max-width:100%;
          width:1000px;
          margin:0 auto;
        }

        p.caption {
          font-family:'Times New Roman', Times, serif;
          font-size:16px;
          font-weight:bold;
        }

      }

      ul, li {
        font-size:18px;
      }

      .to-database {
        padding:30px 0 50px 0;
      }

    }

  }

}