.search {

  main {
    min-height:640px;
  }

  .search-bar {
    padding:50px 30px;
  }

  .search-results {
    padding: 0 30px 30px 30px;

    .ant-spin.ant-spin-dot .anticon {
      color:#6b6b6b;
      font-size:30px;
    }

    .ant-table {
      cursor:pointer;
      border-top:1px solid #dfdfdf;

      td {
        padding:4px;
      }

      th:first-child {
        border-left:1px solid #dfdfdf;
      }

      th:last-child {
        border-right:1px solid #dfdfdf;
      }

      td:first-child {
        font-size:0;
        border-left:1px solid #dfdfdf;
 
        img {
          width:60px;
          height:60px;
          display:block;
          border-radius:2px;
        }
      }

      td:last-child {
        border-right:1px solid #dfdfdf; 
      }

      tr:last-child td {
        border-bottom:1px solid #dfdfdf; 
      }

    }

  }
  
}