// Petrol
$signature_color:#2a6b6c;
$signature_color_dark:#1e635d;
$signature_color_darker:#134a44;

// Rooftile orange
// $signature_color: #af5030;
// $signature_color_darker: #cb5526;
// $signature_color_dark: #d23500;

html, body {
  margin:0;
  padding:0;
  min-height:100%;
  width:100%;
  box-sizing:border-box;
  background-color:#f0f4f9 !important;
  scroll-behavior: smooth;
}

.nowrap {
  white-space:nowrap;
}

#container {
  min-height:100%;
}

a.btn  {
  padding:14px 40px 14px 30px;
  border-radius:3px;
  display:inline-block;
  background-color:$signature_color;
  color:#fff !important;
  text-decoration:none !important;

  .anticon {
    padding-right:10px;
  }
  
}

a.btn:hover {
  background-color:lighten($signature_color, 5);
}

// Remove padding on tablet devices
@media only screen and (min-device-width: 1112px) {

  #container { 
    padding:20px;
  }
   
  #app {
    border-radius:3px;
  }

}

#app {
  max-width:1400px;
  margin:0 auto;
  overflow:hidden;
  background-color:#fff;
  box-shadow:0 0 4px rgba(0,0,0,0.05);
  position:relative;

  // Cf. https://github.com/Leaflet/Leaflet/issues/3575#issuecomment-688644225
  .leaflet-tile-container img {
    width: 256.5px !important;
    height: 256.5px !important;
  }

  nav.content-nav {
    width:100%;
    background-color:$signature_color;    
    padding:10px 16px 7px 20px;
    color:#fff;
    text-align:right;
    background-position:4px 3px;
    background-repeat:no-repeat;
    background-size:244px 54px;

    .nav-item-wrapper {
      display:inline-block;
      position:relative;
      padding:0 10px 0 20px;
      text-decoration:none;
    }

    .nav-item {
      display:flex;
      flex-direction:column;
      align-items:center;
      cursor:pointer;
      color:#fff;

      .anticon {
        display:block;
        font-size:20px;
      }

      label {
        padding-top:3px;
        font-size:12px;
      }

    }
  }

  a {
    color:$signature_color_dark;
    text-decoration:underline;
  }

}

