@import "../App.scss";

.house.loaded {

  header {
    
    .jumbo {
      height:340px;
      position:relative;
      background-size:cover;

      .jumbo-contents {
        position:absolute;
        left:50px;
        bottom:30px;
        line-height: 2.5em;
        
        * {
          color:#fff;
          margin:8px 0 0 0;  
          padding:0;
          text-shadow:0 0 6px rgba(0,0,0,.9);
          font-family: 'Slabo 27px', serif;
        }

        h1 {
          font-size:40px;
        }

        h2 {
          font-size:30px;
          font-weight:normal;
        }
        
        .house-properties {
          font-size:26px;
        }
        
      }

    }

    .mini-map {
      position:absolute;
      top:225px;
      right:60px;

      .leaflet-container {
        width:360px;
        height:270px;
        border-radius:12px;
        border:5px solid #fff;
        overflow:hidden;  
      }

      .layer-toggle {
        text-align:right;
        padding:6px 8px;

        .ant-switch {
          background-color:#acacac;
        }

        .ant-switch-inner {
          padding:0 4px;
        }

        .ant-switch-checked {
          background-color:#acacac;
        }

        .ant-switch:focus {
          -webkit-box-shadow:none;
                  box-shadow:none;
        }

        .ant-click-animating-node {
          display:none;
        }

      }

    }

  }

  main {
    
    .section {
      padding:50px 50px 0 50px;
      font-size:17px;
      font-family: 'Nunito Sans', serif;

      h3 {
        font-size:22px;
        color:$signature_color_dark;
      }
    }

    .section.key-properties {
      padding-top:30px;
      padding-right:440px;

      ul, li {
        padding:0;
        margin:0;
        list-style-type:none;
      }

      li {
        padding:2px 0;
      }

      .anticon {
        font-size:20px;
      }

      .anticon, label {
        padding-right:5px;
        color:$signature_color_dark;
      }

    }

    .section.atrium {

      p {
        display:inline;
      }

    }

    .section.images, .section.plans {

      .ant-image {
        margin-right:20px;
        border-radius:3px;
        overflow:hidden;
      }
      
      .image-caption-wrapper {
        position:fixed;
        bottom:0;
        left:0;
        width:100%;
        z-index:9999;
        padding:40px;
        display: flex;
        justify-content: center;

        .image-caption {
          display:inline-block;
          background-color:#fff;
          z-index:9999;
          padding:20px;
          border-radius:3px;
          box-shadow:0 0 4px rgba(0,0,0,0.4);

          h4 {
            font-weight:bold;
            padding:0;
            margin:0;
          }

          p {
            padding:0;
            margin:0;
          }
          
        }
      
      }

    }

    .section.bibliography {

      p {
        font-family:'Times New Roman', Times, serif;
        font-size:18px;
        white-space: pre-line;
      }

    }

    .section.cite-as {
      
      p {
        position:relative;
        background-color:#f0f4f9;
        padding:20px 60px 20px 20px;
        border-radius:6px;
        font-family:'Times New Roman', Times, serif;
        font-size:18px;

        a {
          color:#545454;
          text-decoration:underline;
          white-space:nowrap;
        }

        .anticon.copy {
          position:absolute;
          top:12px;
          right:12px;
          font-size:20px;
          color:#adadad;
        }

        .anticon.copy:hover {
          color:#5f5f5f;
        }
      }

    }

    .section:last-child {
      padding-bottom:80px;
    }
  
  }

}

.house.loading, .house.error {
  height:calc(100vh - 40px);
  display:flex;
  justify-content:center;
  align-items:center;

  .spinner, .message {
    text-align:center;
  }

  .message {
    color:#dd1919;
  }

  .anticon {
    font-size:30px;
  }

  .label {
    padding-top:10px;
  }

}
